import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { Result, Button, Spin } from 'antd';
import { LoadingOutlined, SmileOutlined } from '@ant-design/icons';
import AuthService from '../../services/auth.service';

type VerificationData = {
    code: string;
    id: string;
}

const VerifyEmail = () => {
    const { code, id } = useParams<VerificationData>();
    const [loading, setLoading] = useState(true);
    const [verified, setVerified] = useState(false);

    useEffect(() => {
        (async () => {
            try {
                await AuthService.verifyEmail(code, id);
                setVerified(true);
                setLoading(false);
            } catch {
                setVerified(false);
                setLoading(false);
            }
        })();
    }, []);


    return (
        <>
            {
                loading ?
                    <Spin indicator={<LoadingOutlined style={{ fontSize: 35 }} spin />} /> : (
                        verified ?
                            <Result
                                icon={<SmileOutlined />}
                                title="Email —É—Å–ø—ñ—à–Ω–æ –ø—ñ–¥—Ç–≤–µ—Ä–¥–∂–µ–Ω–æ"
                                extra={
                                    <Button type="primary">
                                        <Link to="/">
                                            –£–≤—ñ–π—Ç–∏
                                </Link>
                                    </Button>
                                }
                            /> :
                            <Result
                                status="error"
                                title="–ù–µ –≤–¥–∞–ª–æ—Å—è –ø—ñ–¥—Ç–≤–µ—Ä–¥–∏—Ç–∏ email"
                                subTitle="–°–ø—Ä–æ–±—É–π—Ç–µ –ø—Ä–µ–π—Ç–∏ –∑–∞ –ø–æ—Å–∏–ª–∞–Ω–Ω—è–º –∑–Ω–æ–≤—É –∞–±–æ —Å—Ç–≤–æ—Ä—ñ–Ω—å –Ω–æ–≤–∏–π –∞–∫–∞—É–Ω—Ç"
                                extra={[
                                    <Button type="primary" key="console">
                                        <Link to="/">
                                            –ù–∞ –≥–æ–ª–æ–≤–Ω—É
                                </Link>
                                    </Button>
                                ]}
                            />)
            }
        </>
    );
};

export default VerifyEmail;