import React, { useState } from 'react';
import { Form, Input, Button, Alert } from 'antd';
import { MailOutlined, LockOutlined } from '@ant-design/icons';
import { Link, useHistory } from 'react-router-dom';
import styles from './AuthForm.module.css';
import AuthService from '../../services/auth.service';
import { validationMessages } from '../../helpers/intex';


type User = {
    email: string;
    password: string;
};

const RegistrationForm = () => {
    let history = useHistory();
    const [form] = Form.useForm();
    const [error, setError] = useState(false);
    const [loading, setLoading] = useState(false);

    const onFinish = async ({ email, password }: User) => {
        setLoading(true);

        try {
            await AuthService.register(email, password);
            await AuthService.login(email, password);
            history.push("/email-confirmation");
        } catch {
            setLoading(false);
            setError(true);
        }
    };

    return (
        <>
            {
                error &&
                <Alert message="–¶–µ–π email –≤–∂–µ –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î—Ç—å—Å—è" type="error" className={styles.loginError} showIcon />
            }
            <Form
                form={form}
                name="registration"
                className={styles.loginForm}
                onFinish={onFinish}
                validateMessages={validationMessages}
                size="large"
            >
                <Form.Item
                    name="email"
                    rules={[
                        {
                            required: true,
                            message: '–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å email'
                        },
                        {
                            type: 'email'
                        }
                    ]}
                >
                    <Input prefix={<MailOutlined className="site-form-item-icon" />} placeholder="Email" />
                </Form.Item>
                <Form.Item
                    name="password"
                    rules={[
                        {
                            required: true,
                            message: '–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å –ø–∞—Ä–æ–ª—å'
                        },
                        {
                            min: 6,
                            message: '–ú—ñ–Ω—ñ–º–∞–ª—å–Ω–∞ –¥–æ–≤–∂–∏–Ω–∞ 6 —Å–∏–º–≤–æ–ª—ñ–≤'
                        }
                    ]}
                    hasFeedback
                >
                    <Input
                        prefix={<LockOutlined className="site-form-item-icon" />}
                        type="password"
                        placeholder="–ü–∞—Ä–æ–ª—å"
                    />
                </Form.Item>
                <Form.Item
                    name="confirm"
                    dependencies={['password']}
                    hasFeedback
                    rules={[
                        {
                            required: true,
                            message: '–ë—É–¥—å –ª–∞—Å–∫–∞, –ø—ñ–¥—Ç–≤–µ—Ä–¥—ñ—Ç—å –ø–∞—Ä–æ–ª—å',
                        },
                        ({ getFieldValue }) => ({
                            validator(_, value) {
                                if (!value || getFieldValue('password') === value) {
                                    return Promise.resolve();
                                }
                                return Promise.reject('–ü–∞—Ä–æ–ª—ñ –Ω–µ –∑–±—ñ–≥–∞—é—Ç—å—Å—è');
                            },
                        }),
                    ]}
                >
                    <Input
                        prefix={<LockOutlined className="site-form-item-icon" />}
                        type="password"
                        placeholder="–ü–æ–≤—Ç–æ—Ä—ñ—Ç—å –ø–∞—Ä–æ–ª—å"
                    />
                </Form.Item>
                <Form.Item className={styles.submitContainer}>
                    <Button type="primary" htmlType="submit" className={styles.loginFormButton} loading={loading}>
                        –ó–∞—Ä–µ—î—Å—Ç—Ä—É–≤–∞—Ç–∏—Å—å
                </Button>
                </Form.Item>
            </Form>
        </>
    );
};

export default RegistrationForm;